import React from 'react'
import { Link } from 'gatsby'
import { OutboundLink } from 'gatsby-plugin-google-analytics'

import { rhythm, scale } from '../utils/typography'

class Layout extends React.Component {
  render() {
    const { title, children } = this.props
    const header = <header style={{ marginBottom: rhythm(1.5), marginTop: 0, display: 'grid', gridTemplateColumns: `75% 25%` }}>
        <main>
          <h1 style={{ ...scale(0.7), margin: 0 }}>
            <Link style={{ boxShadow: `none`, textDecoration: `none`, color: `inherit` }} to={`/`}>
              {title}
            </Link>
          </h1>
        </main>
        <aside style={{display: `flex`, alignItems: `center`, justifyContent: `flex-end`}}>
          <a href={`https://twitter.com/Iiterature`}>
            Twitter
          </a>
        </aside>
      </header>

    return (
      <div
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(24),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        {header}
        {children}
        <footer>
          Â© {new Date().getFullYear()}
          <OutboundLink style={{float: `right`}} href='/rss.xml'>RSS Feed</OutboundLink>
        </footer>
      </div>
    )
  }
}

export default Layout
